import { createSlice } from '@reduxjs/toolkit';

export const spoilersSlice = createSlice({
  name: 'spoilers',
  initialState: {
    filenames: 0,
    descriptions: 0,
    revealedFiles: [] as Array<string>,
    revealedDescriptions: [] as Array<string>
  },
  reducers: {
    showFilenames: (state) => {
      state.filenames = 1;
    },
    hideFilenames: (state) => {
      state.filenames = 0;
    },
    toggleFilenames: (state) => {
        state.filenames ^= 1
    },
    showDescriptions: (state) => {
      state.descriptions = 1;
    },
    hideDescriptions: (state) => {
      state.descriptions = 0;
    },
    toggleDescriptions: (state) => {
        state.descriptions ^= 1
    },
    revealFile: (state,value) => {
      const revealed = new Set(state.revealedFiles)
      revealed.add(value.payload)
      state.revealedFiles = Array.from(revealed)
    },
    revealDescription: (state,value) => {
      const revealed = new Set(state.revealedDescriptions)
      revealed.add(value.payload)
      state.revealedDescriptions = Array.from(revealed)
    },
    updateFile:(state,value)=>{
      state.revealedFiles = Array.from(value.payload)
    },
    updateDescriptions:(state,value)=>{
      state.revealedDescriptions = Array.from(value.payload)
    },
    unrevealFile: (state,value) => {
      const revealed = new Set(state.revealedDescriptions)
      revealed.delete(value.payload)
      state.revealedDescriptions = Array.from(revealed)
    },
    unrevealDescription: (state,value) => {
      const revealed = new Set(state.revealedDescriptions)
      revealed.delete(value.payload)
      state.revealedDescriptions = Array.from(revealed)

    },
    

    
  },
});

export const { showFilenames, hideFilenames, toggleFilenames, showDescriptions, hideDescriptions, toggleDescriptions, revealFile,revealDescription,unrevealDescription,unrevealFile,updateFile,updateDescriptions} = spoilersSlice.actions;

export default spoilersSlice.reducer;

