import React from 'react';
import { IListOfFiles } from '../types/IListOfFiles';

interface IExtractDateFromListedFile {
    row: IListOfFiles;
}
export function extractDateFromListedFile({ row }: IExtractDateFromListedFile): string {
    return row.modified.replace(/(\d{2}:\d{2}:\d{2})\.\d+/g, '$1');
}
